<template>
  <div class="container">
    <div class="container-form">
      <el-form ref="qryParamRef" :model="qryParam" size="small">
        <div class="row-container">
          <div class="left-row">
            <el-row :gutter="20">
              <el-col :span="6">
                <el-form-item :label="$t('order.sourceAddr')" prop="sourceAddr">
                  <el-input
                    v-model="qryParam.sourceAddr"
                    :placeholder="inputPh('order.sourceAddr')"
                    @input="
                      (value) =>
                        (qryParam.sourceAddr = value.replace(/\D/g, '').slice(0, 20))
                    "
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item :label="$t('order.dstAddr')" prop="dstAddr">
                  <el-input
                    v-model="qryParam.dstAddr"
                    :placeholder="inputPh('order.dstAddr')"
                    @input="
                      (value) =>
                        (qryParam.dstAddr = value.replace(/\D/g, '').slice(0, 20))
                    "
                  ></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item :label="$t('order.smsDirection')" prop="smsDirection">
                  <ct-select
                    v-model="qryParam.smsDirection"
                    :placeholder="selectPh('order.smsDirection')"
                    :options="smsDirections"
                  ></ct-select>
                </el-form-item>
              </el-col>
              <el-col :span="6">
                <el-form-item :label="$t('order.sendStatus')" prop="sendStatus">
                  <ct-select
                    v-model="qryParam.sendStatus"
                    :placeholder="selectPh('order.sendStatus')"
                    :options="sendStatusOptions"
                  ></ct-select>
                </el-form-item>
              </el-col>
              <div v-show="powerSearch" class="more-query-group">
                <el-col :span="6">
                  <el-form-item :label="$t('order.sendTime')" prop="submitStartDate">
                    <ct-date-picker
                      :begin.sync="qryParam.submitStartDate"
                      :end.sync="qryParam.submitEndDate"
                    >
                    </ct-date-picker>
                  </el-form-item>
                </el-col>
              </div>
            </el-row>
          </div>
          <div class="right-row">
            <el-form-item>
              <div class="search-button-group">
                <el-button
                  type="primary"
                  icon="el-icon-search"
                  @click="qryCmpParamPage(false)"
                  >{{ $t('common.query') }}</el-button
                >
                <el-button icon="el-icon-refresh" @click="reset">{{
                  $t('common.reset')
                }}</el-button>
                <show-more-button v-model="powerSearch" />
              </div>
            </el-form-item>
          </div>
        </div>
        <div class="tool-button-group">
          <el-button v-compPriv="46" icon="el-icon-view" @click="viewPlain">
            {{ $t('accountMag.viewPlain') }}</el-button
          >
          <BatchExport
            :query-id="2500"
            :params="qryParam"
            :disabled="tableData.length === 0"
          ></BatchExport>
        </div>
      </el-form>
      <el-table
        ref="table"
        v-loading="loading"
        class="container-table"
        :data="tableData"
        :border="true"
        height="100%"
      >
        <el-table-column
          prop="sourceAddr"
          :label="$t('order.sourceAddr')"
          show-overflow-tooltip
          width="200"
        >
        </el-table-column>
        <el-table-column
          prop="dstAddr"
          :label="$t('order.dstAddr')"
          show-overflow-tooltip
          width="200"
        ></el-table-column>
        <el-table-column
          prop="supplierId"
          :label="$t('rule.supplier')"
          show-overflow-tooltip
          min-width="130"
          :formatter="rowFormatter"
        ></el-table-column>
        <el-table-column
          prop="submitContent"
          :label="$t('order.content')"
          show-overflow-tooltip
          min-width="120"
        ></el-table-column>
        <el-table-column
          prop="smsDirection"
          :label="$t('order.smsDirection')"
          :formatter="rowFormatter"
          show-overflow-tooltip
          min-width="110"
        >
        </el-table-column>
        <el-table-column
          prop="sendStatus"
          :label="$t('order.sendStatus')"
          :formatter="rowFormatter"
          show-overflow-tooltip
          min-width="110"
        >
        </el-table-column>
        <el-table-column
          prop="submitDate"
          :label="$t('order.sendTime')"
          show-overflow-tooltip
          min-width="110"
        ></el-table-column>
      </el-table>
      <el-pagination
        v-show="tableData.length > 0"
        background
        :current-page="page.pageIndex"
        align="right"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="page.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="page.total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </div>
    <ViewPlainDialog
      :visible.sync="plainDialogVisible"
      @modifyParam="qryCmpParamPage(false)"
    ></ViewPlainDialog>
  </div>
</template>
<script>
import langMixin from '@/mixins/langMixin'
import fetch from '@/request/fetch'
import BatchExport from '@/components/BatchExport'
import ViewPlainDialog from './ViewPlainDialog.vue'
export default {
  components: { BatchExport, ViewPlainDialog },
  mixins: [langMixin],
  data() {
    return {
      powerSearch: false,
      loading: false,
      plainDialogVisible: false,
      page: {
        pageIndex: 1,
        pageSize: 10,
        total: 0,
      },
      suppliers: [],
      tableData: [],
      qryParam: {
        sourceAddr: '',
        dstAddr: '',
        sendStatus: '',
        submitStartDate: '', // 开始时间
        submitEndDate: '', // 结束时间
        smsDirection: '',
      },
    }
  },
  computed: {
    smsDirections() {
      return [
        {
          value: '',
          label: this.$t('order.all'),
        },
        {
          value: 0,
          label: this.$t('order.directionDown'),
        },
        {
          value: 1,
          label: this.$t('order.directionUp'),
        },
      ]
    },
    sendStatusOptions() {
      return [
        {
          value: '',
          label: this.$t('order.all'),
        },
        {
          value: '1000',
          label: this.$t('order.status1000'),
        },
        {
          value: '1100',
          label: this.$t('order.status1100'),
        },
        {
          value: '1200',
          label: this.$t('order.status1200'),
        },
        {
          value: '1300',
          label: this.$t('order.status1300'),
        },
      ]
    },
  },
  async mounted() {
    this.qryCmpParamPage(false)
    this.suppliers = await fetch(
      '/prodInst/getSupplierListForQuery',
      {},
      { method: 'get' }
    )
  },
  methods: {
    rowFormatter(row, col, val) {
      if (col.property === 'smsDirection') {
        return this.smsDirections.find((item) => item.value === val).label
      } else if (col.property === 'sendStatus') {
        return this.sendStatusOptions.find((item) => item.value === val).label
      } else if (col.property === 'supplierId') {
        return this.suppliers.find((item) => item.attrValue === val).attrValueName
      }
    },
    viewPlain() {
      this.plainDialogVisible = true
    },
    qryCmpParamPage(pageChangeFlag) {
      if (!pageChangeFlag) {
        this.page.pageIndex = 1
        this.page.pageSize = 10
      }
      const param = {
        ...this.qryParam,
        ...this.page,
      }
      this.loading = true
      fetch('/smsRecord/qrySmsRecordPage', param, { method: 'post' })
        .then((res) => {
          this.tableData = res.list || []
          this.page.total = res.total || 0
        })
        .finally(() => {
          this.loading = false
        })
    },
    handleSizeChange(val) {
      this.page.pageSize = val
      this.page.pageIndex = 1
      this.qryCmpParamPage(true)
    },
    handleCurrentChange(val) {
      this.page.pageIndex = val
      this.qryCmpParamPage(true)
    },
    // 重置
    reset() {
      this.$refs.qryParamRef.resetFields()
      this.$nextTick(() => this.qryCmpParamPage(false))
    },
  },
}
</script>
<style lang="scss" scoped>
@import '@/assets/css/page.scss';
.action-button-group {
  display: flex;
  .el-button:first-child {
    padding-left: 0;
  }
}
.tool-button-group {
  .el-button:first-child {
    margin-right: 10px;
  }
}
</style>
