<!--
使用参考：
<batchExport :queryId="1005" :params="usageBatchParam" :showMsg="userTable.length===0"></batchExport>
params需要与queryId配置相匹配
showMsg为true时，不允许导出，只予以提示。用在先查询出数据才能导出场景

-->

<template>
  <el-dropdown :hide-on-click="true" class="fr" :size="size" trigger="click">
    <el-button plain :size="size" :disabled="disabled">
      {{ $t('common.export') }}<i class="el-icon-arrow-down el-icon--right"></i>
    </el-button>
    <el-dropdown-menu slot="dropdown">
      <el-dropdown-item @click.native="exportCsv">CSV</el-dropdown-item>
      <el-dropdown-item @click.native="exportTxt">TXT</el-dropdown-item>
    </el-dropdown-menu>
  </el-dropdown>
</template>

<script>
import fetch from '@/request/fetch'

export default {
  components: {},
  props: {
    queryIdList: { type: Array, default: () => [] },
    size: {
      type: String,
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    params: {
      type: Object,
      default: () => {
        return {}
      },
    },
    sheetName: {
      type: String,
      default: null,
    },
    queryId: {
      type: [Number, String],
      default: '',
    },
    showProd: {
      type: String,
      default: '',
    },
  },
  data() {
    return {}
  },
  computed: {},
  methods: {
    exportCsv() {
      this.exprt('csv')
    },
    exportTxt() {
      this.exprt('txt')
    },
    async exprt(fileType, origin) {
      const param = { queryId: this.queryId, fileType: fileType, ...this.params }
      let data = []
      if (this.queryId === '2040') {
        data = await fetch('/prodInst/exportSimCardList', param, { method: 'post' })
      } else {
        data = await fetch('/batch/export', param, { method: 'get' })
      }
      if (data && data.length > 0) {
        this.errAlert(data)
        return
      }
      this.success()
    },
    success() {
      const self = this
      const h = this.$createElement
      this.$msgbox({
        title: this.$t('batchUpload.actionSuccess'),
        type: 'success',
        center: true,
        message: h('p', { class: 'fs12' }, [
          this.$t('batchUpload.task'),
          h(
            'a',
            { class: 'cor-blue', on: { click: self.viewProgress } },
            this.$t('batchUpload.process')
          ),
        ]),
        showCancelButton: false,
        confirmButtonText: this.$t('batchUpload.know'),
      })
        .then(() => {})
        .catch(() => {})
    },
    viewProgress() {
      this.$msgbox.close()
      this.$router.push('/common/download')
    },
    errAlert(errMsgList) {
      const h = this.$createElement
      const msg = h('div', { class: 'error-msg-box' }, [
        ...(function () {
          const arr = []
          errMsgList.forEach((item) => {
            arr.push(h('p', { class: 'cor-red' }, item))
          })
          return arr
        })(),
      ])
      this.$msgbox({
        title: this.$t('batchUpload.exportFail'),
        type: 'warning',
        center: true,
        message: msg,
        showCancelButton: false,
        confirmButtonText: this.$t('batchUpload.know'),
      })
        .then(() => {})
        .catch(() => {})
    },
  },
}
</script>

<style lang="scss" scoped></style>
